<template>
  <el-new-card>
    <template slot="header">
      <span>待办事项</span>
    </template>
    <div class="to-do-list" :style="{ height }">
      <div v-for="(item, index) in list" :key="'to-do-list-' + index">
        <i>{{ index + 1 }}</i><el-link :type="item.isDanger ? 'danger' : 'info'" :href="item.type | fmtHref(item.id)">{{ item.text }}</el-link>
      </div>
    </div>
  </el-new-card>
</template>

<script>
import ElNewCard from '@/components/ElNewCard'

export default {
  name: 'to-do-list',
  components: { ElNewCard },
  props: {
    /**
     * 待办事项列表
     * list的数据结构
     * list = [
     *   {
     *     id: [Number, String] // 事项对应的(账户/活动/结算)的id
     *     text: String, // 标题
     *     isDanger: Boolean // 是否标红
     *     type: String // 事项类型
     *   }
     * ]
     */
    list: {
      type: Array,
      default: () => []
    },
    height: { // 待办事项的高度
      type: String,
      default: '236px'
    }
  },
  filters: {
    // 事项链接，根据事项类型和id设定
    fmtHref (val, id) {
      let path = ''
      switch (val) {
        case 'acctBudgetAmountLine':
        case 'acctImprestAmountLine':
        case 'acctAddAmountAppl':
        case 'acctUseImpAppl': path = '/#/crdtAcctMgmt/acctDetail'; break
        case 'acctAppl': path = '/#/crdtAcctMgmt/acctApplDetail'; break
        case 'actBudgetAmountLine':
        case 'actImprestAmountLine':
        case 'actAmountLack':
        case 'actAddAmountAppl':
        case 'actUseImpAppl':
        case 'actUnissue': path = '/#/activityMgmt/actDetail'; break
        case 'actAppl': path = '/#/activityMgmt/actApplDetail'; break
        case 'stmt': path = '/#/settlementMgmt/settlementDetail'; break
      }

      return `${path}?id=${id}`
    }
  }
}
</script>

<style lang="scss" scoped>
.to-do-list {
  margin: -5px 0;
  height: 236px;
  font-size: 12px;
  overflow: auto;

  &>div {
    display: flex;
    padding: 6px 0;
    border-top: 1px solid #EBEEF5;
    &:first-child {
      padding-top: 0;
      border-top: 0;
    }
    .el-link {
      font-size: 12px;
      line-height: 16px;
      &.el-link--info {
        color: #606266;
        &:hover {
          color: #909399;
        }
      }
    }

    i {
      margin-right: 8px;
      font-style: normal;
      line-height: 16px;
    }
  }
}
</style>
